* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Inter', 'Arial', sans-serif !important;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

custom-textarea:focus-visible, .custom-textarea:focus {
  outline: none;
  border-color: #59BFCE !important;
}

.select--heading {
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  font-size: 22px !important;
  color: #858584 !important;
}

.select--bordered-input {
  border: 2px solid #C7C7C7;
  padding: 12px 31px;
  font-size: 18px;
  /*width: 100%;*/
  border-radius: 15px;
  color: #C7C7C7;
}

.custom--input-mask, .custom--input-mask::placeholder {
  border: 0;
  background-color: transparent;
  outline: none;
  color: #C7C7C7;
  font-size: 18px;
  font-weight: bold;
}


.code--input {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100% !important;
  gap: 10px;
}

.code--input div > input:focus {
  border-color: #00C0CF;
  caret-color: #00C0CF;
}

.code--input-error input {
  border-color: red !important;
  color: red !important;
}

.navigate--btn.MuiButton-root, .custom--btn.MuiButton-root, .custom--btn-active {
  position: relative;
  background-color: #F5F5F5;
  padding: 7px 25px;
  color: #858584;
  text-transform: uppercase;
  font-weight: bold;
  width: 100%;
  border: none;
  z-index: 1;
}

.custom--btn.MuiButton-root {
  font-size: 18px;
  border-radius: 15px;
}

.navigate--btn.MuiButton-root:hover, .custom--btn.MuiButton-root:hover, .active .navigate--btn.MuiButton-root {
  color: #fff;
  background-color: unset;
  border: none;
}

.navigate--btn.MuiButton-root::after, .custom--btn.MuiButton-root::after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-image: linear-gradient(to right, #71D8D7, #00C0CF);
  opacity: 0;
  z-index: -1;
  border-radius: 50px;
  transition: opacity .1s ease-in-out;
  border: none;
}

.MuiButton-root.navigate--btn:hover::after, .MuiButton-root.custom--btn:hover::after, .active .navigate--btn.MuiButton-root::after {
  opacity: 1;
  border: none;
}


.MuiTypography-root.footer--copyright {
  font-family: Gilroy, Montserrat, sans-serif;
  font-size: 16px;
  color: #737373;
  font-weight: 400;
}

.about--paragraph .MuiTypography-root {
  margin-bottom: 20px;
  display: block;
  font-size: 22px;
  font-weight: 500 !important;
}

.card--grey {
  background-color: #F5F5F5;
  border-radius: 15px;
  padding: 20px;
  width: 100%;
}

.card--grey .MuiTypography-root, .card--white-small .MuiTypography-root {
  font-weight: bold;
  font-size: 18px;
  color: #858584;
}

.card--grey > .MuiTypography-root:first-of-type {
  margin-bottom: 18px;
}

.card--white-small, .card--white-small-15 {
  background-color: #fff;
  text-align: center;
  max-height: 60px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card--white-small {
  border-radius: 6px;
  max-height: 60px;
}

.card--white-small-15 {
  border-radius: 15px;
}

.card--white-small small.MuiTypography-root {
  font-size: 10px;
  font-weight: normal;
}

.file-manager--heading > .MuiTypography-root {
  font-size: 25px;
  color: #858584;
  line-height: 1.2;
}

.file-manager--middle > .MuiTypography-root {
  font-size: 18px;
  font-weight: bold;
  color: #858584;
}

.MuiInput-root::after {
  border-bottom: 1px solid #C4C4C4;
}

.application--complete-heading.MuiTypography-root {
  font-weight: bold;
  font-size: 28px;
  color: #858584;
}

.application--complete-subheading.MuiTypography-root {
  font-size: 20px;
  font-weight: 500;
  color: #858584;
}

.rating--heading {
  color: #858584;
  font-size: 30px;
}

.rating--heading h4.MuiTypography-root {
  font-weight: bold;
}

.rating--stars > span > span {
  margin: 0 15px;
}

.custom--btn-white.MuiButton-root {
  background-color: #fff;
  color: #858584;
  border-radius: 8px;
  width: 100%;
}
.custom--btn-white.MuiButton-root:hover {
  transition: background-color .3s ease-in-out;
  background-color: #C8C7C7;
}

.custom--btn-gray.MuiButton-root {
  background-color: #F5F5F5;
  border-radius: 15px;
  text-transform: uppercase;
  color: #858584;
}

.custom--btn-gray.MuiButton-root:hover {
  background-color: #e2e1e1;
  transition: background-color .3s ease-in-out;
}

.custom--btn-white.MuiButton-root, .custom--btn-gray.MuiButton-root {
  width: 100%;
  font-size: 18px;
  font-weight: bold;
}

.application--btn.MuiButton-root {
  margin-top: 40px !important;
  color: #fff !important;
  border: 2px solid #fff !important;
  transition: all .3s ease-in-out;
  padding: 7px 25px;
  border-radius: 33px;
}

.application--btn.MuiButton-root:hover {
  color: #59BFCE !important;
  background-color: #fff !important;
}

.verification--heading.MuiTypography-root {
  font-size: 35px;
  color: #858584;
  font-weight: bold;
  text-transform: uppercase;
}

.verification--text.MuiTypography-root {
  font-size: 22px;
  color: #858584;
}

.rate > div {
  background-image: url('assets/media/icons/star-outline.svg');
  background-size: cover;
  background-position: center;
}

.rate > div:hover,
.rate div:hover ~ div {
  background-image: url('assets/media/icons/star.svg');
}

.bg-star {
  background-image: url('assets/media/icons/star.svg') !important;
}

.toast-show {
  left: 50%;
  top: 2rem;
  opacity: 0;
  font-size: 1.2rem;
  color: rgb(7, 122, 255);
  border-radius: 2rem;
  transform: translateX(-50%);
  animation: fallDown .4s ease .3s forwards;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

@keyframes fallDown {
  0% {
    margin-top: -8rem;
    opacity: 0;
  }
  100% {
    margin-top: 0;
    opacity: 1;
  }
}

@media (max-width: 640px) {
  .min-h-screen {
    overflow: hidden;
  }
}

@media only screen and (max-width: 768px) {
  .custom--btn.MuiButton-root {
      font-size: 14px;
  }
}

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Inter Regular'), local('InterRegular'), 
   url('./assets/fonts/InterRegular.ttf') 
   format('ttf');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, 
    U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, 
    U+2215, U+FEFF, U+FFFD;
}

.fadeLeft {
  animation: fadeLeft .2s ease forwards;
}

.fadeFromRight {
  animation: fadeFromRight .4s ease;
}

@keyframes fadeLeft {
  100% {
    transform: translateX(-200px);
    opacity: 0;
  }
}

@keyframes fadeFromRight {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.hidden-scroll::-webkit-scrollbar {
  display: none;
}

.hidden-scroll {
  scrollbar-width: none;
  -ms-overflow-style: none; 
}

.custom-checkbox {
  visibility: hidden;
  position: relative;
  z-index: 999;
}

.custom-checkbox + label::before {
  content: "";
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  vertical-align: bottom;
  font-weight: bold;
  width: 1.3rem;
  height: 1.3rem;
  border-radius: 15%;
  border-style: solid;
  border-width: 0.1rem;
  border-color: gray;
  visibility: visible;
  position: absolute;
  top: 5px;
  left: 0;
}

.custom-checkbox:checked + label::before {
  content: "✓";
  color: white;
  text-align: center;
  background: rgb(59 130 246);
  border-color: rgb(59 130 246);
}

.custom-checkbox.error + label::before {
  border-color: #e11d48;
}

.checkAnim {
  content: "";
  width: 30px;
  height: 30px;
  position: absolute;
  left: 10px;
  top: 10px;
  border-radius: 50%;
  background: #000;
  transform-origin: 100% 50%;
}

.checkAnim.light {
  background-color: #fff;
}

.checkAnim.active {
  animation: checking .5s ease-in forwards;
}

@keyframes checking {
  100% {
    transform: scaleX(0);
  }
}

.fadeRight {
  transform: translateX(-100px);
  opacity: 0;
  transition: transform .5s ease-in, opacity .5s ease-in;
}

.fadeRight.active {
  transform: translateX(0);
  opacity: 1;
}

@keyframes fadeRight {
  0% {
    transform: translateX(-150px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.review-grid {
  display: grid;
  grid-template-columns: 1fr;
}

@media screen and (min-width: 999px) {
  .review-grid {
    grid-template-columns: minmax(200px, 1fr) minmax(max-content, 1fr);
  }
}

@media screen and (min-width: 1280px) {
  .review-grid {
    grid-template-columns: minmax(200px, 1fr) minmax(200px, 1fr) min-content;
  }
}
